
.user {
  height: 100%;
  padding: 15px 20px 0 20px;
  background: #fff;
}
.user .search {
    line-height: 100px;
    text-align: center;
}
.user .search .el-autocomplete {
      width: 65%;
}
.user .new {
    height: 40px;
}
.user .table {
    height: calc(100% - 180px);
}
.user .table .el-table {
      border-top: 1px solid #ebeef5;
}
.user .label-position {
    margin: 0 0 15px 0;
    height: 40px;
    display: block;
}
.user .label-position .label-position-left {
      width: 120px;
      text-align: right;
      vertical-align: middle;
      float: left;
      font-size: 14px;
      color: #606266;
      line-height: 40px;
      padding: 0 12px 0 0;
}
.user .label-position .label-position-right {
      line-height: 40px;
      position: relative;
      font-size: 14px;
      margin-left: 120px;
}
.user .label-position .label-position-right .el-select.el-select--mini {
        width: 80%;
}
.user .inline-input {
    width: 80%;
}

