.img-403 {
  position: relative;
  height: 100%;
}
.img-403 .img-box {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 650px;
    text-align: center;
    font-size: 16px;
}
.img-403 img {
    display: block;
    margin: 0 auto;
}
.img-403 .tips-403 {
    margin-top: 10px;
    font-size: 20px;
    color: #979797;
}
.img-403 .tips-403 .name {
      margin: 10px auto 0;
      width: 500px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: break-word;
      white-space: nowrap;
}

.img-404 {
  position: relative;
  min-height: calc(100% - 50px);
}
.img-404 img {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
